import React from 'react';
import PropTypes from 'prop-types';
import Heading from 'components/UI/Heading/Heading';
import TextLink from 'components/UI/TextLink/TextLink';
import { FaDivide, FaDollarSign, FaSearchLocation, FaLevelUpAlt } from 'react-icons/fa';
import IconCard from 'components/IconCard/IconCard';
import AmenitiesWrapper, { AmenitiesArea } from './Amenities.style';
import { TextButton } from '../SinglePageView.style';
import { Element } from 'react-scroll';

const Amenities = ({ titleStyle, linkStyle, from, to }) => {

  const fractionText = () => {
    if(to == null || from == null)
    {
      return `Each NFT represents fractions.`;
    }
    let n = to - from + 1;

    return `Each NFT represents 1/${n}.`;
  }

  return (
    <Element name="amenities" className="Amenities">
      <AmenitiesWrapper>
        <Heading as="h2" content="Features" {...titleStyle} />
        <AmenitiesArea>
          <IconCard icon={<FaDivide />} title={fractionText()} />
          <IconCard icon={<FaDollarSign />} title="Rewards paid to the wallet." />
          <IconCard icon={<FaSearchLocation />} title="Always know the location of your asset via GPS." />
          <IconCard icon={<FaLevelUpAlt />} title="Lock the NFT and use it while on vacation to Orlando." />
        </AmenitiesArea>
        {/* <TextButton>
          <TextLink link="#1" content="Show all features" {...linkStyle} />
        </TextButton> */}
      </AmenitiesWrapper>
    </Element>
  );
};

Amenities.propTypes = {
  titleStyle: PropTypes.object,
  linkStyle: PropTypes.object,
};

Amenities.defaultProps = {
  titleStyle: {
    color: '#2C2C2C',
    fontSize: ['17px', '20px', '25px'],
    lineHeight: ['1.15', '1.2', '1.36'],
    mb: ['14px', '20px', '30px'],
  },
  linkStyle: {
    fontSize: '15px',
    fontWeight: '700',
    color: '#008489',
  },
};

export default Amenities;
