import React, { useState } from 'react';
import { Button, Input, notification, Spin  } from 'antd';
import HtmlLabel from 'components/UI/HtmlLabel/HtmlLabel';
import ReservationFormWrapper, {
  FormActionArea,
  FieldWrapper,
} from './Reservation.style.js';
import AxiosFetch from 'library/hooks/AxiosFetch';
import mintNft, { approveAll } from 'web3/Premier';
import { LoadingOutlined } from '@ant-design/icons';

const loadingIcon = <LoadingOutlined style={{ fontSize: 24, color: "white" }} spin />;

const RenderReservationForm = ( props ) => {
  
  const { collectionId, account, setTokenAmountFromProps } = props;
  const [tokenAmount, setTokenAmount] = useState(1);
  const [isLoading, setIsLoading] = useState(false);
  const [isApproveLoading, setIsApproveLoading] = useState(false);
  const [isApprovedForStaking, setIsApprovedForStaking] = useState(true);

  const openNotificationWithIcon = (type, title, description) => {
    notification[type]({
      message: title,
      description: description,
      placement: 'bottomRight'
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    setIsLoading(true);
    let url = '/checkMintable';
    const body = {
      collectionId: collectionId,
      account: account
    }
    let res = await AxiosFetch(url, body, "POST");
    
    if(!res.data.success)
    {
      setIsLoading(false);
      openNotificationWithIcon('error', 'Error!', res.data.message)
      return;
    }
    if(!account)
    {
      setIsLoading(false);
      openNotificationWithIcon('error', 'Error!', 'You must connect your wallet!')
      return;
    }
    if(!tokenAmount || tokenAmount < 1)
    {
      setIsLoading(false);
      openNotificationWithIcon('error', 'Error!', 'You have to mint at least 1 NFT.')
      return;
    }
    
    if(res.data.success)
    {
      const price = res.data.price
      const timestamp = res.data.timestamp
      const isApproved = res.data.isApproved

      await mintNft(price, tokenAmount, timestamp, collectionId - 1, account).then(
        (data) => {          
          if (data.success == false) {
            if (data.type == "estimategas") {
              openNotificationWithIcon('error', 'Error!', `Insufficient funds to mint. Please consider the price ${price} ETH, and some gas fee!`)              
            } else if (data.type == "mint") {
              openNotificationWithIcon('error', 'Error!', `Error happened while processing, please try again later`)                            
            }
          }
          else
          {
            setIsApprovedForStaking(isApproved);
            handleSaveOwnership(tokenAmount, collectionId, account, isApproved);
          }
          setIsLoading(false);
        }
      );   
      setIsLoading(false);
    }
  };  

  const handleSaveOwnership = async (tokenAmount, collectionId, account, isApproved) => {
    const body = {
      tokenAmount: tokenAmount,
      collectionId: collectionId,
      address: account
    }

    let url = '/saveOwnerInfo';
    
    let res = await AxiosFetch(url, body, "POST");

    if(res.data.success)
    {
      if(isApproved)
      {
        openNotificationWithIcon('success', 'Congratulations!', `You have minted ${tokenAmount} token(s). Please click account button for using.`);      
      }
      else {
        openNotificationWithIcon('success', 'Congratulations!', `You have minted ${tokenAmount} token(s). Please approve your account.`);              
        handleApprove();
      }
    }
  }  
  
  const handleApprove = async() => {
    setIsApproveLoading(true);
    await approveAll(account).then(
      (data) => {            
        if (data.success == false) {
          openNotificationWithIcon('error', 'Error!', `Error happened while approving, please try again later`)                            
        }
        else
        {
          openNotificationWithIcon('success', 'Success', `Your account has been approved for staking. Please click account button for staking or renting.`)                        
          setIsApprovedForStaking(true);
        }
        setIsApproveLoading(false);
      }
    );
  }

  const setTotalPrice = (e) => {
    setTokenAmount(e);
    setTokenAmountFromProps(e);
  }

  return (
    <ReservationFormWrapper className="form-container" onSubmit={handleSubmit}>      
      <FieldWrapper>
        <HtmlLabel htmlFor="guests" content="How many" />       
        <Input type="number" defaultValue="1" onChange={(e) => setTotalPrice(e.target.value)}/>        
      </FieldWrapper>
      <FormActionArea>
        <Button htmlType="submit" type="primary" disabled={isLoading}>
          { !isLoading && <span> Mint </span> }
          { isLoading && <Spin indicator={loadingIcon} /> }
        </Button>

        {
          !isApprovedForStaking && 
            <Button type="primary" onClick={handleApprove} style={{ marginTop: "20px" }} disabled={isApproveLoading}>
              { !isApproveLoading && <span> Approve </span> }
              { isApproveLoading && <Spin indicator={loadingIcon} /> }
            </Button>
        }
      </FormActionArea>
    </ReservationFormWrapper>
  );
};

export default RenderReservationForm;
