import React, { useState, Fragment } from 'react';
import PropTypes from 'prop-types';
import Card from 'components/UI/Card/Card';
import Heading from 'components/UI/Heading/Heading';
import Text from 'components/UI/Text/Text';
import TextLink from 'components/UI/TextLink/TextLink';
import RenderReservationForm from './RenderReservationForm';

const CardHeader = ({ price, priceStyle, pricePeriodStyle, linkStyle, amount }) => {
  return (
    <Fragment>
      <Heading
        content={
          <Fragment>
            { price * amount } ETH<Text as="span" content="" {...pricePeriodStyle} />
          </Fragment>
        }
        {...priceStyle}
      />
      {/* <a href="https://turo.com/us/en/drivers/9170089" target="blank" > Contact Owner </a> */}
    </Fragment>
  );
};

export default function Reservation(props) {

  const { collectionId, price, account } = props;

  const [tokenAmount, setTokenAmount] = useState(1);
  const setTokenAmountFromProps = (e) => {
    setTokenAmount(e);
  }

  return (
    <Card
      className="reservation_sidebar"
      header={<CardHeader price={ price } amount={tokenAmount}/>}
      content={<RenderReservationForm collectionId={ collectionId } account={account} setTokenAmountFromProps={setTokenAmountFromProps}/>}
      footer={
        <p>          
        </p>
      }
    />
  );
}

CardHeader.propTypes = {
  priceStyle: PropTypes.object,
  pricePeriodStyle: PropTypes.object,
  linkStyle: PropTypes.object,
};

CardHeader.defaultProps = {
  priceStyle: {
    color: '#2C2C2C',
    fontSize: '25px',
    fontWeight: '700',
  },
  pricePeriodStyle: {
    fontSize: '15px',
    fontWeight: '400',
  },
  linkStyle: {
    fontSize: '15px',
    fontWeight: '700',
    color: '#008489',
  },
};
