import React, { Fragment, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useLocation } from 'library/hooks/useLocation';
import Sticky from 'react-stickynode';
import { Row, Col, Modal, Button } from 'antd';
import Container from 'components/UI/Container/Container';
import Loader from 'components/Loader/Loader';
import useWindowSize from 'library/hooks/useWindowSize';
import Description from './Description/Description';
import Amenities from './Amenities/Amenities';
import Location from './Location/Location';
import Review from './Review/Review';
import Reservation from './Reservation/Reservation';
import BottomReservation from './Reservation/BottomReservation';
import TopBar from './TopBar/TopBar';
import SinglePageWrapper, { PostImage } from './SinglePageView.style';
import PostImageGallery from './ImageGallery/ImageGallery';
import useServerApi from 'library/hooks/useServerApi';
import isEmpty from 'lodash/isEmpty';

const SinglePage = (props) => {

  const { account } = props;

  let { slug } = useParams();
  const { href } = useLocation();
  const [isModalShowing, setIsModalShowing] = useState(false);
  const { width } = useWindowSize();

  const body = {
    collectionId: slug
  }

  let url = '/getOneCollection';
  
  const { data, loading } = useServerApi(url, "POST", body);

  if (isEmpty(data) || loading) return <Loader />;
  const {
    rating,
    ratingCount,
    price,
    name,
    gallery,
    formattedAddress,
    city,
    state,
    country,
    content,
    lat,
    lng,
    from,
    to,
    type
  } = data[0];

  const location = {
    lat,
    lng,
    formattedAddress,
    city,
    state,
    country
  };

  const locationMap = {
    "image": {
			"id": 1,
			"url": "http://s3.amazonaws.com/redqteam.com/tripfinder-images/hotel-1.jpg",
			"thumb_url": "http://s3.amazonaws.com/redqteam.com/tripfinder-images/hotel-1_thumb.jpg"
		},
		"location": {
			"id": 1,
			"lat": lat,
			"lng": lng,
			"formattedAddress": formattedAddress,
			"zipcode": "",
			"city": city,
			"state_long": state,
			"state_short": state,
			"country_long": "United States Of America",
			"country_short": "USA"
		},
  };

  return (
    <SinglePageWrapper>
      <PostImage>
        <img
          className="absolute"
          src={gallery[0].original}
          alt="Listing details page banner"
        />
        <Button
          type="primary"
          onClick={() => setIsModalShowing(true)}
          className="image_gallery_button"
        >
          View Photos
        </Button>
        <Modal
          visible={isModalShowing}
          onCancel={() => setIsModalShowing(false)}
          footer={null}
          width="100%"
          maskStyle={{
            backgroundColor: 'rgba(255, 255, 255, 0.95)',
          }}
          wrapClassName="image_gallery_modal"
          closable={false}
        >
          <Fragment>
            <PostImageGallery gallery={gallery} />
            <Button
              onClick={() => setIsModalShowing(false)}
              className="image_gallery_close"
            >
              <svg width="16.004" height="16" viewBox="0 0 16.004 16">
                <path
                  id="_ionicons_svg_ios-close_2_"
                  d="M170.4,168.55l5.716-5.716a1.339,1.339,0,1,0-1.894-1.894l-5.716,5.716-5.716-5.716a1.339,1.339,0,1,0-1.894,1.894l5.716,5.716-5.716,5.716a1.339,1.339,0,0,0,1.894,1.894l5.716-5.716,5.716,5.716a1.339,1.339,0,0,0,1.894-1.894Z"
                  transform="translate(-160.5 -160.55)"
                  fill="#909090"
                />
              </svg>
            </Button>
          </Fragment>
        </Modal>
      </PostImage>

      <TopBar title={name} shareURL={href} media={gallery} />

      <Container>
        
          {type == "car" &&
            <Row gutter={30} id="reviewSection" style={{ marginTop: 30 }}>
              <Col xl={24}>
                <Description
                  content={content}
                  title={name}
                  location={location}
                  rating={rating}
                  ratingCount={ratingCount}
                />
                <Amenities from={from} to={to}/>
                <Location location={locationMap} />
              </Col>
            </Row>
          }
          {type == "plane" &&
            <Row gutter={30} id="reviewSection" style={{ marginTop: 30 }}>
              <Col xl={16}>
                <Description
                  content={content}
                  title={name}
                  location={location}
                  rating={rating}
                  ratingCount={ratingCount}
                />
                <Amenities from={from} to={to}/>
                <Location location={locationMap} />
              </Col>
              <Col xl={8}>
                {width > 1200 ? (
                  <Sticky
                    innerZ={999}
                    activeClass="isSticky"
                    top={202}
                    bottomBoundary="#reviewSection"
                  >
                    <Reservation collectionId={slug} price={price} account={account}/>
                  </Sticky>
                ) : (
                  <BottomReservation
                    title={name}
                    price={price}
                    rating={rating}
                    ratingCount={ratingCount}
                    collectionId={slug} 
                    account={account}
                  />
                )}
              </Col>   
            </Row>
          }
        <Row gutter={30}>
          <Col xl={16}>
            {/* <Review
              reviews={reviews}
              ratingCount={ratingCount}
              rating={rating}
            /> */}
          </Col>
          <Col xl={8} />
        </Row>
      </Container>
    </SinglePageWrapper>
  );
};

export default SinglePage;
